@import 'normalize.scss';
@import 'reset.scss';

* {
    box-sizing: border-box;
    transition: 1s all; }

@font-face {
    font-family: 'MontserratT';
    src: url('../fonts/Montserrat-Regular.ttf');
    font-weight: regular;
    font-style: normal;
    font-display: swap; }

@font-face {
    font-family: 'MontserratT';
    src: url('../fonts/Montserrat-SemiBold.ttf');
    font-weight: bold;
    font-style: normal;
    font-display: swap; }

html {
    font-family: 'MontserratT';
    font-weight: regular; }

$color-blue: #5290EA;
$color-orange: #F28B64;
$shadow-figure: 0px 4px 4px rgba(0, 0, 0, 0.25);

.container {
    max-width: 1140px;
    margin: 0 auto;
    position: relative; }
.main-section {
    position: relative;
    min-height: 800px;
    background: #F0F0F0;
    padding-top: 16px;
    overflow: hidden;
    &__nav-container {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        height: 69px; }
    &__logo {
        height: 69px; }
    &__content-container {
        display: flex;
        flex-direction: row;
        position: relative; }
    &__first-elipse {
        position: absolute;
        top: 129px;
        right: 80px; }
    &__second-elipse {
        position: absolute;
        top: 570px;
        right: -220px; } }

.nav-menu {
    &__list {
        display: flex;
        flex-direction: row; }
    &__item {
        margin-right: 64px;
        font-size: 18px;
        font-weight: bold;
        &:last-child {
            margin-right: 0; }
        a {
            text-decoration: none;
            color: #000; } } }

.left-wrapper {
    position: relative;
    &__img {
        position: relative;
        z-index: 5; }
    &__rectangle-blue {
        position: absolute;
        width: 565.95px;
        height: 220px;
        left: -50px;
        top: 150px;
        background: $color-blue;
        box-shadow: $shadow-figure;
        border-radius: 100px;
        transform: rotate(-64deg);
        z-index: 2; }
    &__rectangle-orange {
        position: absolute;
        width: 618.43px;
        height: 220px;
        left: -30px;
        top: 160px;
        background: $color-orange;
        box-shadow: $shadow-figure;
        border-radius: 100px;
        transform: rotate(-50.81deg);
        z-index: 3; }
    &__rectangle-gray {
        position: absolute;
        width: 608.97px;
        height: 220px;
        left: 10px;
        top: 190px;
        background: #E2E2E2;
        box-shadow: $shadow-figure;
        border-radius: 100px;
        transform: rotate(-36.15deg);
        z-index: 1; } }

.right-wrapper {
    width: 372px;
    position: absolute;
    top: 150px;
    right: 76px;
    z-index: 5;
    &__icons-box {
        margin-bottom: 1px;
        padding-left: 10px; }
    &__title {
        margin-bottom: 5px; }
    &__text {
        font-size: 16px;
        margin-bottom: 20px; } }



.icons {
    display: inline-flex;

    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        border: 1px solid $color-blue;
        border-radius: 100px;
        margin-right: 17px;
        &:last-child {
            margin-right: 0; }
        i {
            font-size: 21px; } } }

.title {
    font-size: 72px;
    position: relative;
    font-weight: bold;
    &__hr {
        width: 65px;
        position: absolute;
        bottom: 0;
        margin: 0;
        border: none;
        border-top: 1px solid black; } }

.buttons {
    display: inline-flex;
    &__main-btn {
        margin-right: 32px;
        width: 200px;
        height: 60px;
        background: $color-orange;
        box-shadow: $shadow-figure;
        border-radius: 100px;
        border: none;
        color: white;
        font-size: 18px;
        font-weight: bold; }
    &__icon-btn {
        max-width: 140px;
        display: inline-flex;
        align-items: center;
        img {
            margin-right: 12px; } }
    &__text {
        font-size: 16px; } }

.elipse {
    width: 194px;
    height: 194px;
    background: linear-gradient(143.92deg, #ED7B50 14.85%, #488AEA 53.25%, #2C2C2C 85.74%);
    border-radius: 360px;
    display: flex;
    align-items: center;
    justify-content: center;
    &_big {
        width: 282px;
        height: 282px; }
    &__subtract {
        width: 129.33px;
        height: 129.33px;
        background: #F0F0F0;
        border-radius: 360px;
        &_big {
           width: 188px;
           height: 188px; } } }

//   Вторая секция

.second-section {
    overflow: hidden;
    min-height: 700px;
    &__container {
        padding-top: 32px;
        position: relative; }
    &__title {
        margin-bottom: 40px; }
    &__cards-box {
        display: inline-flex;
        justify-content: center;
        margin-bottom: 25px; }
    &__cards-items {
        margin-right: 30px;
        &:last-child {
            margin-right: 0; } }
    &__icons-box {
        display: flex;
        justify-content: space-evenly; }
    &__first-elipse {
        position: absolute;
        top: 80px;
        right: -60px;
        z-index: -1; }
    &__second-elipse {
        position: absolute;
        top: 364px;
        left: -60px;
        z-index: -1; } }

.cards-items {
    position: relative;
    width: 360px;
    height: 360px;
    background: #E2E2E2;
    border-radius: 10px;
    display: flex;
    align-items: flex-end;
    &__img {
        height: 335px; }
    &__text {
        width: 280px;
        font-size: 36px;
        text-align: center;
        transform: rotate(90deg);
        position: absolute;
        top: 144px;
        right: -45px;
        font-weight: bold;
        transition: 0.6s all; } }

.cards-items_orange {
    background: $color-orange; }

.cards-items_blue {
    background: $color-blue; }

// Иконки
.icons-item {
    display: inline-flex;
    &__img {
        width: 80px;
        margin-right: 20px; }
    &__text {
        font-size: 18px;
        span {
            font-size: 48px;
            font-weight: bold; } } }

// Третья секция

.third-section {
    min-height: 640px;
    background: #f0f0f0;
    z-index: 0;
    position: relative;
    overflow: hidden;
    &__container {
        display: flex;
        flex-direction: row;
        align-items: center;
        padding-left: 167px;
        padding-top: 46px;
        justify-content: space-between; }
    &__right-wrapper {
        max-width: 480px; }
    &__right-title {
        margin-bottom: 20px; }
    &__text {
        font-size: 18px;
        margin-bottom: 30px; }
    &__first-elipse {
        position: absolute;
        top: 140px;
        left: 110px;
        z-index: -1; }
    &__figure {
        position: absolute;
        top: 249px;
        left: 15px;
        z-index: -1; }
    &__second-elipse {
        position: absolute;
        right: -220px;
        top: -120px; } }


.title__hr_top {
    bottom: 75px; }
.footer {
    &__container {
         min-height: 272px;
         display: flex;
         justify-content: flex-end;
         align-items: center; }
    &__img {
        position: absolute;
        left: -250px;
        bottom: 0; }
    &__title {
        margin-bottom: 20px; } }
// Медиа адаптив

@media (max-width: 1199px) {
    .container {
        width: 960px; }
    .main-section {
        min-height: 690px; }
    .left-wrapper {
        &__img {
            width: 750px; }
        &__rectangle-blue {
            width: 500px;
            height: 200px; }
        &__rectangle-orange {
            width: 550px;
            height: 200px; }
        &__rectangle-gray {
            width: 500px;
            height: 200px; } }
    .right-wrapper {
        right: 20px; }
    .main-section__first-elipse {
        right: 10px; }
    .main-section__second-elipse {
        top: 500px; }
    .second-section {
        min-height: 600px;
        &__cards-box {
            display: flex;
            justify-content: center; } }
    .cards-items {
        width: 300px;
        height: 300px;
        margin-right: 20px; }
    .cards-items__img {
        height: 270px; }
    .cards-items__text {
        font-size: 30px;
        top: 120px; }
    .icons-item__img {
        margin-right: 10px; }
    .third-section__container {
        padding-left: 40px; }
    .third-section__right-wrapper {
        max-width: 450px; } }

@media (max-width: 991px) {
    .main-section {
        min-height: 530px;
        &__first-elipse {
            top: 70px; }
        &__second-elipse {
            top: 330px;
            right: -190px; } }
    .container {
        width: 720px; }
    .left-wrapper {
        img {
            width: 550px; } }
    .left-wrapper {
        &__rectangle-blue {
            width: 380px;
            height: 130px; } }
    .left-wrapper {
        &__rectangle-orange {
            width: 410px;
            height: 130px; } }
    .left-wrapper {
        &__rectangle-gray {
            width: 380px;
            height: 130px; } }
    .title {
        font-size: 60px; }
    .right-wrapper {
        top: 70px;
        width: 300px;
        &__text {
            margin-bottom: 10px;
            font-size: 14px; } }
    .buttons {
        &__main-btn {
            font-size: 14px;
            margin-right: 10px;
            width: 160px;
            height: 50px; }
        &__icon_btn {
            font-size: 14px; } }
    .elipse {
        width: 150px;
        height: 150px;
        &__subtract {
            width: 110px;
            height: 110px; }
        &__subtract_big {
            width: 170px;
            height: 170px; } }

    .elipse_big {
        width: 250px;
        height: 250px; }

    .second-section {
        min-height: 450px;
        &__container {
            padding-top: 20px; }
        &__title {
            margin-bottom: 25px; }
        &__cards-box {
            margin-bottom: 10px; }
        &__first-elipse {
            right: -35px; }
        &__second-elipse {
            top: 262px;
            left: -35px; } }
    .cards-items {
        width: 220px;
        height: 220px;
        &__img {
            height: 200px; }
        &__text {
            font-size: 22px;
            top: 92px;
            right: -75px; } }
    .icons-item {
        &__img {
            width: 50px; }
        &__text {
            font-size: 14px;
            span {
                font-size: 30px; } } }
    .third-section {
        min-height: 510px;
        &__left-title {
            display: none; }
        &__right-wrapper {
            max-width: 600px; }
        &__container {
            justify-content: flex-start;
            padding-left: 20px;
            padding-top: 20px; }
        &__right-title {
            margin-bottom: 10px; } }

    .title {
        &__hr_top {
            bottom: 60px; } }

    .footer {
        &__img {
            width: 500px; }
        &__title {
            margin-bottom: 10px; }
        &__container {
            min-height: 210px; } } }


@media (max-width: 767px) {
    .container {
        width: 540px; }
    .nav-menu {
        position: fixed;
        top: 0;
        right: -100%;
        height: 100vh;
        background: $color-blue;
        width: 50%;
        z-index: 10;
        transition: 0.6s all;
        &__list {
            flex-direction: column;
            justify-content: space-evenly;
            align-items: center;
            height: 100%; }
        &__item {
            margin-right: 0;
            font-size: 20px; } }

    .hamburger {
        width: 30px;
        position: fixed;
        top: 40px;
        right: 30px;
        height: 14px;
        z-index: 15;
        span {
            height: 2px;
            width: 100%;
            display: block;
            background: #000;
            margin-bottom: 4px;
            transition: 1s all;
            &:last-child {
                margin-bottom: 0; } } }
    .hamburger_active {
        span {
            margin-bottom: -2px;
            &:nth-child(1) {
                transform: translateY(3px) rotate(-45deg); }
            &:nth-child(2) {
                display: none; }
            &:nth-child(3) {
                transform: translateY(3px) rotate(45deg); } } }
    .nav-menu_active {
        right: 0px; }

    .left-wrapper {
        img {
            width: 340px; } }
    .left-wrapper {
        &__rectangle-blue {
            display: none; } }
    .left-wrapper {
        &__rectangle-orange {
            display: none; } }
    .left-wrapper {
        &__rectangle-gray {
            display: none; } }
    .title {
        font-size: 50px; }
    .right-wrapper {
        top: 30px;
        width: 245px; }
    .buttons {
        flex-direction: column;
        &__main-btn {
            margin-right: 0px;
            margin-bottom: 10px; } }
    .elipse {
        width: 150px;
        height: 150px;
        &__subtract {
            width: 110px;
            height: 110px; }
        &__subtract_big {
            width: 140px;
            height: 140px; } }

    .elipse_big {
        width: 230px;
        height: 230px; }
    .main-section {
        min-height: 400px;
        &__first-elipse {
            top: 47px; }
        &__second-elipse {
            top: 230px;
            right: -130px; } }

    .cards-items {
        height: 180px;
        width: 180px;
        justify-content: center;
        margin-right: 10px;
        &__img {
            left: 50px;
            height: 110px;
            position: absolute; }
        &__text {
            position: absolute;
            left: -70px;
            font-size: 18px;
            top: 16px;
            width: auto;
            transform: rotate(0deg); } }
    .second-section {
        &__second-elipse {
            top: 170px; }
        &__cards-box {
            margin-bottom: 20px; }
        &__icons-box {
            flex-wrap: wrap; }
        &__icons-item {
            width: 35%;
            margin-bottom: 20px; } }
    .third-section {
        .right-wrapper__button {
            flex-direction: row;
            .buttons__main-btn {
                margin-right: 30px; } } }
    .title__hr_top {
        bottom: 36px; }
    .title {
        font-size: 40px; }
    .footer__img {
        width: 400px; } }

@media (max-width: 575px) {
    .container {
        width: 90%; }
    .main-section {
        min-height: 340px; }
    .right-wrapper {
        width: 90%;
        right: 16px;
        &__text {
            margin-bottom: 14px;
            width: 299px; }
        &__title {
            margin-bottom: 14px; }
        &__button {
            margin-bottom: 50px; } }
    .left-wrapper {
        &__img {
            display: none; } }
    .buttons {
        flex-direction: row; }
    .buttons__icon-btn {
        &__img {
            width: 40px; } }
    .second-section {
        &__icons-item {
            width: 50%; }
        &__cards-box {
            flex-direction: column; }
        &__cards-items {
            width: 100%;
            height: 260px;
            margin-right: 0;
            margin-bottom: 20px; }
        .cards-items {
            &__img {
                height: 230px;
                left: 10px; }
            &__text {
                width: 209px;
                font-size: 22px;
                text-align: center;
                position: absolute;
                top: 110px;
                left: 40%; } }
        &__second-elipse {
            top: 800px; } }
    .footer__img {
            width: 85%; }
    .third-section__container {
        padding-left: 0; } }

@media (max-width: 370px) {
    .second-section {
        .cards-items {
            &__text {
                transform: rotate(90deg); } }
        &__icons-item {
            width: 100%; } }
    .elipse_big {
        width: 200px;
        height: 200px; }
    .third-section {
        .right-wrapper__button {
            flex-direction: column-reverse; }
        .buttons__main-btn {
            margin-right: 0;
            margin-top: 20px; } } }
